	module.exports = {"data":{"site":{"siteMetadata":{"title":"signalwerk"}},"markdownRemark":{"html":"<h1 id=\"ibm-plex-sans\"><a href=\"#ibm-plex-sans\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>IBM Plex Sans</h1>\n<p><small>Stephanie Fuchs</small></p>\n<div class=\"col1to12\">\n<p><img src=\"/IAD2017.schriftmuster/IBMPlexSans-5763e2467ccf4d9c536abe0a7e5b168b.svg\" alt=\"IBM Plex Sans\"></p>\n</div>\n<ol>\n<li>Versalhöhe H</li>\n<li>Doppelstöckiges (Zweistöckiges) a mit schrägem Anstrich</li>\n<li>Geschlossener Punzen</li>\n<li>Dreistöckiges g mit geradem Ohr (Fähnchen)</li>\n<li>Schräger Abstrich beim e</li>\n<li>Verlängerte Oberlänge beim t,f,b</li>\n<li>Haarlinie bei a,m,b,u,r,g,n</li>\n<li>Ecke rund/eckig a,g,f,t</li>\n</ol>\n<h2 id=\"design\"><a href=\"#design\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Design</h2>\n<p>Die IBM PLex Superfamilie besteht aus vier Schriftfamilien, wovon jede je acht Schnitte umfasst. Diese Schrift hat eine lange Geschichte. Der von IBM entwickelte Font löste die Helvetica als Firmenschrift ab. Das ursprünglich 1956 von Paul Rand entworfene Fimenlogo wird man höchst wahrscheinlich auch erkennen, wenn man nur einen einzelnen Buchstaben daraus sieht. Die Herausforderung bei IBM war nun, wie dass eine dazu passende Schriftfamilie aussehen sollte.</p>\n<p>Mike Abbink setzte sich mit der Agentur Bold Monday zusammen und entwickelte die IBM Plex Superfamilie, welche mit ihren verschiedenen Schriftfamilien einen vielseitigen Einsatz und trotzdem ein einheitliches Erscheinungsbild ermöglicht. Für jede Sparte der passende Schnitt. So kann diese flexible Schrift von Entwicklern, auf Produktverpackungen und auf ibm.com verwendet werden. IBM arbeitet nun an Zeichen für diverse Sprachen, welche im laufe von 2018 gelauncht werden. Auf die chinesischen Zeichen werden wir bis 2019 warten müssen. </p>\n<p>Der Font IBM Plex ist frei verfügbar. Er kann auf Google Fonts heruntergeladen werden und könnte theoretisch sogar auch eines Tages eine Systemschriften bei Apple werden. Dass der Free-Font möglicherweise auch von anderen Firmen für ihre Logos oder als Firmenschrift verwendet werden könnte, dem steht man bei IBM positiv gegenüber.</p>\n<h2 id=\"designer\"><a href=\"#designer\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Designer</h2>\n<p>Die IBM Plex wurde von 2016–2017 von Michael «Mike» Abbink (US-amerikanischer Schriftgestalter und Typograf), verantwortlich für Konzept und Direktion Design, in Zusammenarbeit mit der holländischen Agentur Bold Monday designt. Das Team von Bold Monday für die IBM Plex bestand aus folgenden Designern:</p>\n<p>Paul van der Laan, Pieter van Rosmalen, Barbara Bigosińska, Diana Ovezea, Aleksandra Samuļenkova, Jasper Terra, Edgar Walthert, Marko Hrastovec, Pablo Gamez</p>\n<p>Abbink startete seine Karriere bei MetaDesign in San Francisco. Nach drei Jahren war er Mitgründer von Methond, wo er Creative Director Corporate Identity, interaktives Design und Webdesign für Kunden wie Adobe, Autodesk, Gucci und weiteren war. Danach war Abbink einige Jahre für Apple als Design Director Packaging tätig, wo er ein wichtiges Update für die Corporate Identitiy entwarf, als diese auf die Font Myriad wechselte.</p>\n<p>Bevor Mike Abbink zu Saffron, eine Agentur für globale Marken- und Innovationsberatung, kam, arbeitete er für Wolff Olins in San Francisco und New York als Design Director.</p>\n<p>Der letzte von Abbinks zahlreichen Awards war für die von ihm entwickelte Font FF Kevit (Autodesk, Gensler und Olympus, CDU)</p>\n<p>Fonts: FF Kevit (Slab), FF Milo (Slab), Brando, GE Sans/Serif</p>\n<h4 id=\"quellen\"><a href=\"#quellen\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Quellen</h4>\n<ol>\n<li><a href=\"https://www.ibm.com/thought-leadership/innovation_explanations/article/mike-abbink.html?1=1\">IBM</a></li>\n<li><a href=\"https://en.wikipedia.org/wiki/IBM_Plex\">Wikipedia</a></li>\n<li><a href=\"https://www.ibm.com/plex/\">IBM Plex</a></li>\n<li><a href=\"https://www.mikeabbink.com/\">Mike Abbink</a></li>\n</ol>","frontmatter":{"date":"April 05, 2018","title":"IBM Plex Sans"}}},"pathContext":{"slug":"/fonts/stephanie/"}}

